<template lang="html">
  <x-header>
    {{team.name}}
    <a slot="right" @click="joinTeam" href="javascript:;">
      加入队伍
    </a>
  </x-header>
  <div class="team_logo">
    <div class="logo">
      <img :src="team.logo.logo.url" alt="" />
      <input type="file" accept="image/jpg,image/jpeg,image/png,image/gif" multiple />
    </div>
  </div>
  <div class="weui_cells">
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-at"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.name}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
    <div class="weui_cell">
      <div class="weui_cell_hd">
        <i class="fa fa-tag"></i>
      </div>
      <div class="weui_cell_bd weui_cell_primary">
        <p style="padding-left:10px;">{{team.sign}}</p>
      </div>
      <div class="weui_cell_ft"></div>
    </div>
  </div>
  <div class="weui_cells_title">{{team.name}}的球员：</div>
    <div class="weui_cells">
      <div class="weui_cell" v-for="user in team.users">
        <div class="weui_cell_hd"><img src="data:image/png;base64," alt="" style="width:20px;margin-right:5px;display:block"></div>
        <div class="weui_cell_bd weui_cell_primary">
          <p>{{user.name}}</p>
        </div>
        <div class="weui_cell_ft">身高{{user.height}}cm</div>
      </div>
    </div>
  </div>
  <toast :show.sync="loadFail" type="text">加载失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        team: {
          logo: {logo: {url: ''}},
          users: []
        },
        loadFail: false,
        joinFail: false
      }
    },
    route: {
      data (transition) {
        this.$http.get(this.$config.API + '/teams/' + this.$route.params.tid + '.json').then(function (response) {
          // 登录成功
          this.$set('team', response.data.team)
        }, function (response) {
          // 登录失败
          this.$set('loadFail', true)
        })
      }
    },
    methods: {
      joinTeam () {
        let accessToken = localStorage.getItem('ruzun_user_token')
        if (accessToken === null) {
          this.$route.router.go({name: 'signin'})
          return
        }
        this.$http.post(
          this.$config.API + '/team_users.json',
          {team_id: this.team.id},
          {headers: {'Authorization': 'User ' + accessToken}})
        .then(function (response) {
          // 登录成功
          console.log(response.data)
        }, function (response) {
          // 登录失败
          this.$set('joinFail', true)
        })
      }
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



